import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-phonenumber-details-popup',
  templateUrl: './phonenumber-details-popup.component.html',
  styleUrls: ['./phonenumber-details-popup.component.scss']
})
export class PhonenumberDetailsPopupComponent implements OnInit {
  customerData: any;


  constructor(
    public dialogRef: MatDialogRef<PhonenumberDetailsPopupComponent>,
    @Inject(MAT_DIALOG_DATA) private data: any
  ) {
    this.customerData=data;
   }

  ngOnInit(): void {
    console.log("resp::",this.customerData)
  }

  Continue(): void {
    this.dialogRef.close("Yes");
  }

  Cancel(): void {
    this.dialogRef.close("No");
  }

}
