import { FingerPrintCaptureService } from './../../../shared/services/finger-print-capture.service';
import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { API_URL } from 'app/shared/services/finger-print-capture.service';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'app-kyc-face-scan',
  templateUrl: './kyc-face-scan.component.html',
  styleUrls: ['./kyc-face-scan.component.scss']
})
export class KycFaceScanComponent implements OnInit {

  public customerId: string;
  isScanned:boolean=false;
  refreshedCustId: any;
  constructor(public dialogRef: MatDialogRef<KycFaceScanComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, private snack: MatSnackBar,
    private fingerPrintService: FingerPrintCaptureService) { }

  ngOnInit(): void {
    this.customerId = sessionStorage.getItem('customer_id');
    this.refreshedCustId=this.data.customerId;
    console.log('customer id :: ', this.customerId ,  this.refreshedCustId);
  }

  closeClick(isScanned) {
    console.log("close click")
    // [routerLink]="['/others/kyc']" 
    this.dialogRef.close({
      message: 'Confirm',
      isScanned:isScanned
    });
    // this.router.navigateByUrl('/others/kyc'); 
  }

  scan() {
    const obj = {
      fileUrl: `${API_URL}/rest/upload/downloadDocs/?fileName=face_biometric.jpg`
    };
    this.saveBiometric(obj, 'FACE', this.refreshedCustId);
  }

  saveBiometric(capFingerPrint, fingerName, cId) {
    console.log('save biometric')
    let screen = 'kyc-verification';
    this.fingerPrintService.saveBiometric(capFingerPrint, fingerName, cId, screen)
      .subscribe(data => {
        console.log("after save ", data);
        this.isScanned=true;
        // this.cdr.markForCheck();
        this.snack.open('Captured ' + fingerName + ' Finger', 'OK', {
          duration: 4000,
          verticalPosition: 'top',
          horizontalPosition: 'right'
        });
      });
  }
  done(){
    this.closeClick('isScanned') ;
    }

}
